.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}
.dialogsItems {
    padding: 10px;
}

.dialogsItems .active {
    color: gold;
}

.messages {
    padding: 10px;
}
.messages .message {
 color: black;
}
.avatar {
    width: 50px;
    height: 60px;
    border-radius: 50%;
}